.training {
  width: 100%;
  margin: 3rem 0;
  display: grid;
  grid-template-columns: repeat(2, 1fr);
  height: 100%;
}
.training .left {
  text-align: center;
  margin: auto;
  padding: 1rem 4rem;
  max-width: 600px;
}
.training .left p {
  margin: 1.2rem 0;
}
.training .right {
  width: 100%;
}

.right .img-cointainer {
  display: flex;
  grid-template-columns: repeat(12, 1fr);
  position: relative;
  align-items: center;
  text-align: center;
}
.right .img {
  border: 1px solid #333;
  width: 100%;
  max-width: 350px;
  height: 300px;
  object-fit: cover;
  border-radius: 10px;
}
.right .other-left {
  width: 50%;
}
.img:hover {
  width: 500px;
  height: 500px;
  transition: all 0.5s ease;
}
.image-stack {
  padding: 10px;
}

.right .other-right {
  width: 50%;
}
.right {
  display: flex;
  flex-direction: row;
  flex-wrap: wrap;
  justify-content: center;
  align-items: center;
  gap: 16px;
  max-width: 1400px;
  margin: 0 auto;
  padding: 20px 10px;
}

.right .top {
  grid-column: 1 / span 8;
  grid-row: 1;

  z-index: 6;
}
.right .bottom {
  grid-column: 4 / -1;
  grid-row: 1;
  z-index: 5;
}
@media only screen and (max-width: 1140px) {
  .training {
    grid-template-columns: 1fr;
  }
  .training .img {
    max-width: 100%;
  }
}
