#video {
  position: fixed;
  width: 100%;
  height: 100%;
  object-fit: cover;
  z-index: -1;
}
.hero .content .prof {
  vertical-align: middle;
  width: 100px;
  height: 100px;
  border-radius: 100%;
}

.hero {
  height: 100vh;
  width: 100%;
  object-fit: contain;
}
.hero .content {
  text-align: center;
  position: absolute;
  margin: auto;
  width: 100%;
  height: 50%;
  top: 40vh;
}
.hero.content h1 {
  font-size: 4rem;
  color: #fff;
}
.hero p {
  font-size: 1.8rem;
  font-weight: 200;
  text-transform: uppercase;
  margin-bottom: 1.6rem;
  color: #fff;
}
.hero .btn {
  margin: 1rem, 0.2rem;
}
@media only screen and (max-width: 640px) {
  .content h1 {
    font-size: 2rem;
  }
  .content p {
    font-size: 1.4rem;
    margin-bottom: 1.6rem;
  }
}
